<template>
  <el-form :model="ruleForm2" :rules="rules2" ref="ruleForm2" label-position="left" label-width="0px" class="demo-ruleForm login-container">
    <h3 class="title">系统登录</h3>
    <el-form-item prop="account">
      <el-input type="text" v-model="ruleForm2.account" auto-complete="off" placeholder="账号"></el-input>
    </el-form-item>
    <el-form-item prop="checkPass">
      <el-input type="password" v-model="ruleForm2.checkPass" auto-complete="off" placeholder="密码"></el-input>
    </el-form-item>
    <el-checkbox v-model="checked" checked class="remember">记住密码</el-checkbox>
    <el-form-item style="width:100%;">
      <el-button type="primary" style="width:100%;" @click.native.prevent="handleSubmit2" :loading="logining">登录</el-button>
      <!--<el-button @click.native.prevent="handleReset2">重置</el-button>-->
    </el-form-item>
  </el-form>

</template>
<canvas id="cov" height="713" width="1364" style="height: 713px; width: 1364px"></canvas>
<script>
// import { Qarticles } from '../assets/lib/qarticles.min.js';
var Qarticles = require("../assets/lib/qarticles.min.js")
import { requestLogin } from "../api/api";
//import NProgress from 'nprogress'
export default {
  data() {
    return {
      logining: false,
      ruleForm2: {
        account: "admin",
        checkPass: "123456"
      },
      rules2: {
        account: [
          { required: true, message: "请输入账号", trigger: "blur" }
          //{ validator: validaePass }
        ],
        checkPass: [
          { required: true, message: "请输入密码", trigger: "blur" }
          //{ validator: validaePass2 }
        ]
      },
      checked: true
    };
  },
  methods: {
    handleReset2() {
      this.$refs.ruleForm2.resetFields();
    },
    handleSubmit2(ev) {
      var _this = this;
      this.$refs.ruleForm2.validate(valid => {
        if (valid) {
          //_this.$router.replace('/table');
          this.logining = true;
          //NProgress.start();
          var loginParams = {
            username: this.ruleForm2.account,
            password: this.ruleForm2.checkPass
          };
          requestLogin(loginParams).then(data => {
            this.logining = false;
            //NProgress.done();
            let { msg, code, user } = data;
            if (code !== 200) {
              this.$message({
                message: msg,
                type: "error"
              });
            } else {
              sessionStorage.setItem("user", JSON.stringify(user));
              this.$router.push({ path: "/table" });
            }
          });
        } else {
          console.log("error submit!!");
          return false;
        }
      });
    }
  }
}

  var speed = 80;
  if (/Android|webOS|iPhone|iPad|iPod|BlackBerry/i.test(navigator.userAgent)) {
    speed = 400;
  }
  var covColorFuc = function(dot, w, h) {
    return `rgba(${Math.floor(255 * (1 - dot.x / w))}, ${Math.floor(
      255 * (1 - dot.y / h)
    )},${Math.floor(255 * (dot.speedArr[0] / 100))}, 0.8)`;
  };
  var lineColorFuc = function(dot, w, h) {
    return `rgba(${Math.floor(255 * (1 - dot.x / w))}, ${Math.floor(
      255 * (1 - dot.y / h)
    )},${Math.floor(255 * (dot.speedArr[0] / 100))}, 0.4)`;
  };
  var covSpeedFuc = speed => {
    return speed * (Math.random() * 2 - 1);
  };
  var options = {
    lineLink: {
      count: 2,
      show: true
    },
    color: {
      dotColorFuc: covColorFuc,
      lineColorFuc: lineColorFuc
    },
    dot: {
      physical: true,
      speed: speed,
      vxFuc: covSpeedFuc,
      vyFuc: covSpeedFuc,
      count: 80,
      size: {
        random: true,
        max: 20,
        min: 0
      }
    }
  };

  var qarticles = new Qarticles(document.getElementById("cov"), options);

</script>

<style lang="scss" scoped>
#cov {
    height: 100vh;
    width: 100vw;
}
.login-container {
  /*box-shadow: 0 0px 8px 0 rgba(0, 0, 0, 0.06), 0 1px 0px 0 rgba(0, 0, 0, 0.02);*/
  -webkit-border-radius: 5px;
  border-radius: 5px;
  -moz-border-radius: 5px;
  background-clip: padding-box;
  margin: 180px auto;
  width: 350px;
  padding: 35px 35px 15px 35px;
  background: #fff;
  border: 1px solid #eaeaea;
  box-shadow: 0 0 25px #cac6c6;
  .title {
    margin: 0px auto 40px auto;
    text-align: center;
    color: #505458;
  }
  .remember {
    margin: 0px 0px 35px 0px;
  }
}
</style>


